  changeTab(active, shit) {
    this.catId = null;
    this.catList = []
    this.page = 0;
    // shit 立刻检测  通常进来时不检测
    if (
      this.active == "-1" &&
      this.$route.path != "/" &&
      typeof shit == "string"
    ) {
      this.$router.replace({
        name: "index",
        params: {
          active: active
        }
      });
      return;
    }

    this.active = active;


    if(active != "-1" && this.indexList[active] && this.indexList[active].catId)
      {
   this.two_menu(active);
     }



    if (active != "-1" && !this.indexList[active].children) {
      Vue.prototype.$reqFormPost1(
        "/page/info",
        {
          pageId: this.indexList[active].pageId
        },
        res => {
          if (res.returnCode != 200) {
            this["$Message"].warning(res.message);
            return;
          }
          let a = res.data.filter((item, index) => {
            return item.componentType === "COMPONENT_TYPE_GOODS_TAG";
          });
          res.data.forEach((items, index) => {
            for (let i = 0; i < a.length; i++) {
              if (items.id == a[i].id) {
                items.arrtibleIndex = i + 1;
                a[i].arrtibleIndex = i + 1;
              }
            }
          });
        console.log(res.data);
          (<any>Object).assign(this.indexList[active], {
            children: res.data
          });
          this.indexList.push();
        }
      );
    } 
  }

  pageSize = 12;
