  clearSearch(item) {
    this.removeByValue( this.stockpile ,item);
    localStorage.filterValue = JSON.stringify(this.stockpile);
  }
  removeByValue(arr, val) {
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] == val) {
        arr.splice(i, 1);
        break;
      }
    }
  }
  checKeyword(keyword){
